  </div>
</template>

<script>
// import { useStore } from 'vuex';
// import { reactive, computed } from 'vue';
import Carousel from '@/components/MainPage/Carousel.vue';
import rankingList from '@/components/MainPage/rankingList.vue';
import roomTab from '@/components/MainPage/roomTab.vue';
import swal from 'sweetalert2';
import { mapGetters } from 'vuex';

window.Swal = swal;

const accounts = 'accounts';

export default {
  components: {
    Carousel,
    rankingList,
    roomTab,
  },
  computed: {
    ...mapGetters(accounts, ['userInfo', 'isLogin']),
  },
  setup() {
    const onInputImage = () => {
      const input = document.querySelector('.input-image');
      console.log(input.files[0].type);
    };
    return {
      onInputImage,
    };
  },
  methods: {
    setCookie() {
      this.$cookies.set('nosee', 'Y', '7d');
    },
  },
  created() {
    const check = this.$cookies.get('nosee');
    if (check !== null) { //   
      if (this.userInfo.weight === null && this.isLogin === true && check !== 'Y') {
        this.$swal({
          title: '#',
          input: 'checkbox',
          inputPlaceholder: '   ',
          html:
            '  , <br>     . <br> <a href="mypage/update"><strong>My Page</strong></a> . <hr>',
          icon: 'info',
          showCloseButton: true,
        }).then((res) => {
          if (res.value === 1) {
            this.setCookie();
          }
        });
      }
    }
    if (check === null) { //   
      if (this.userInfo.weight !== null && this.isLogin === true) {
        this.$swal({
          title: '#',
          input: 'checkbox',
          inputPlaceholder: '   ',
          html:
            '  , <br>     . <br> <a href="mypage/update"><strong>My Page</strong></a> . <hr>',
          icon: 'info',
          showCloseButton: true,
        }).then((res) => {
          if (res.value === 1) {
            this.setCookie();
          }
        });
      }
    }
  },
};
</script>

